.App {
  background-color: #FFFFFF;
  text-align: center;
  opacity: 0;
  animation: fadeIn 0.33s forwards;
}

.lukasinfo {
  display: flex;
  padding-top: 1rem;
  padding-bottom: 1.25rem;
  flex-direction: row; /* Ensure items are in a row */
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center items horizontally */
  margin: 0 auto; /* Center the lukasinfo div itself */
}

.namediv {
  display: flex;
  flex-direction: column;
  margin-left: 4rem;
}

.name {
  font-size: 3rem;
  font-weight: bold;
  margin: 0.25rem; /* Set equal margin for top and bottom */
}

.hello, .fsd {
  font-size: 1.5rem;
  margin: 0.25rem; /* Set equal margin for top and bottom */
}

.expframe {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 2rem;
  flex-direction: row; /* Ensure items are in a row */
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center items horizontally */
  gap: 1rem; /* Add some spacing between the boxes */
  flex-wrap: wrap; /* Allow items to wrap to the next line */
}

.exp {
  border: 0.125em solid #000000;
  border-radius: 2rem;
  padding: 1rem; /* Adjust padding to fit more content */
  font-size: 0.875rem; /* Adjust font size to fit more content */
  margin: 0.25rem;
  flex: 1 1 20rem; /* Allow the boxes to grow and shrink */
  max-width: 42.5rem; /* Set a maximum width */
  min-width: 20rem; /* Set a minimum width */
  height: 20rem; /* Set a fixed height for uniform size */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
  overflow: auto; /* Allow scrolling if content overflows */
}

.exp h1{
  margin: 0;
  font-size: 2.25rem;
  padding-bottom: 1rem;
  justify-content: center; /* Center the grid horizontally */
  align-items: center; /* Center items vertically */
  text-align: center;
}

.exp ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2-column grid */
  gap: 0.5rem;
  justify-content: center; /* Center the grid horizontally */
  align-items: center; /* Center items vertically */
  text-align: center; /* Center text inside each list item */
}

.exp li {
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .exp {
    flex: 1 1 100%; /* Make the boxes take full width on smaller screens */
    max-width: 100%;
  }

  .exp ul {
    grid-template-columns: 1fr; /* Change to a single column on smaller screens */
  }
}

.splash-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  display: flex;
  flex-direction: column; /* Stack model and text vertically */
  align-items: center;
  justify-content: center;
  z-index: 9999; /* Ensure it overlays all other content */
}

.model {
  width: 100vw; /* Adjust the size of the model */
  height: 80vh; /* Adjust the size of the model */
  filter: brightness(1.5);
}

.bottom-text {
  text-align: center;
  margin-top: 0.5rem; /* Add some spacing between the model and the text */
}


@keyframes spin3d {
  from {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  to {
    transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}